// HTML
export const toHtml = params => {
  var div = document.createElement('div')
  div.innerHTML = params
  return div.childNodes[0]
}

// IE(IE11)
export const isIE = () => {
  if (!!window.ActiveXObject || 'ActiveXObject' in window) {
    return true
  } else {
    return false
  }
}

// JSoumouseoveronmouseout
export const mouseOverProblem = (e, parentObj) => {
  let bool = false
  let relativeObj = e.fromElement || e.relatedTarget
  // elem: e.fromElemente.toElement, IEonmouseoverrelatedTarget
  // relativeObj  
  if (relativeObj !== null) {
    if (document.all) {    // IE,document.allIE
      if (!parentObj.contains(relativeObj)) {    // onmouseover
        bool = true
      }
    } else {    // 
      let reg = parentObj.compareDocumentPosition(relativeObj)  // 
      if (!(reg === 20 || reg === 0)) {
        bool = true
      }
    }
  }
  return bool
}

export const mouseOutProblem = (e, parentObj) => {
  let bool = false
  let relativeObj = e.toElement || e.relatedTarget
  if (relativeObj !== null) {
    if (document.all) {
      if (!parentObj.contains(relativeObj)) {
        bool = true
      }
    } else {
      let reg = parentObj.compareDocumentPosition(relativeObj)
      if (!(reg === 20 || reg === 0)) {
        bool = true
      }
    }
  }
  return bool
}


export const getDateRange = (param) => {
  let start = new Date()
  let end = new Date()
  let nowDayOfWeek = start.getDay()
  let year = start.getFullYear()
  let month = start.getMonth()

  switch (param) {
    case '':
      start.setTime(start.getTime() - 3600 * 1000 * 24 * (nowDayOfWeek + 6))
      end.setTime(end.getTime() - 3600 * 1000 * 24 * nowDayOfWeek)
      break
    case '':
      start.setTime(start.getTime() - 3600 * 1000 * 24 * (nowDayOfWeek - 1))
      end.setTime(end.getTime() + 3600 * 1000 * 24 * (7 - nowDayOfWeek))
      break
    case '':
      if (month === 0) {
        month = 12
        year = year - 1
      }
      if (month < 10) {
        month = '0' + month
      }
      start = year + '-' + month + '-' + '01'
      end = year + '-' + month + '-' + new Date(year, month, 0).getDate()
    case '':
      month++
      start = year + '-' + month + '-' + '01'
      end = year + '-' + month + '-' + new Date(year, month, 0).getDate()
      break
    case '':
      year--
      start = year + '-01-01'
      end = year + '-12-' + new Date(year, 12, 0).getDate()
      break
    case '':
      start = year + '-01-01'
      end = year + '-12-' + new Date(year, 12, 0).getDate()
      break
  }
  return [start, end]
}

// 
const singleSwitch = (data)  => {
  if (data >= 1 && data <= 9) {
    data = '0' + data
  }
  return data
}
export const getDates = (param) => {
  let myDate = new Date()
  let number = 100
  let dateTemp
  let dateArray = []
  let day
  for (let i = number; i > 0; i--) {
    dateTemp = myDate.getFullYear() + '-' + singleSwitch((myDate.getMonth() + 1)) + '-' + singleSwitch(myDate.getDate())
    dateArray.push(dateTemp)
    myDate.setDate(myDate.getDate() - 1)
  }
  switch (param) {
    case 'today':
      day = dateArray[0]
      break
    case 'yesterday':
      day = dateArray[1]
      break
    case 'daybefore':
      day = dateArray[2]
      break
    case 'fourDay':
      day = dateArray[3]
      break
    case 'sevenDay':
      day = dateArray[6]
      break
    case 'eightDay':
      day = dateArray[7]
      break
    case 'fourteenDay':
      day = dateArray[13]
      break
    case 'thirtyDay':
      day = dateArray[29]
      break
    case 'thirty-one-Day':
      day = dateArray[30]
      break
    case 'sixtyDay':
      day = dateArray[59]
      break
  }
  return day
}



