<template>
  <div class="header">
    <div class="logo">
      <img src="./../../assets/img/logo.png" />
      
    </div>

    <el-radio-group v-model="media" size="mini" class="switchMedia" @change="switchMedia">
      <el-radio-button label="PC">PC</el-radio-button>
      <el-radio-button label="M">M</el-radio-button>
    </el-radio-group>

    <el-button type="primary" plain size="mini" @click="backSuper" class="backSuper" v-show="is_Super"></el-button>

    <div class="user-info">
      <span>{{username}}</span>
      <el-dropdown trigger="click" @command="handleCommand">
        <span class="el-dropdown-link">
          <!-- <img class="user-logo" src="./../../assets/img/head2.jpg"> -->
          <i class="el-icon-setting"></i>
        </span>
        <el-dropdown-menu class="exit" slot="dropdown">
          <el-dropdown-item command="loginout"></el-dropdown-item>
        </el-dropdown-menu>
      </el-dropdown>
    </div>
  </div>
</template>

<script>
  import { mapMutations } from 'vuex'
  export default {
    data () {
      return {
        initUsername: '',
        media: 'PC'
      }
    },
    computed: {
      username () {
        let myUsername = localStorage.getItem('ms_username')   //   null
        return myUsername !== null ? myUsername : this.initUsername
      },
      is_Super(){
          let is_Super = localStorage.getItem('is_Super');   //   null
          if (is_Super !== null && is_Super === 'true'){
              return true;
          }else{
              return false;
          }
      }
    },
    methods: {
        backSuper(){
            let that = this
            this.$ajax.get('super_login').then(function (res) {
                if(res.data.error === 1){
                    that.$message.error(res.data.msg);
                }else{
                    localStorage.setItem('ms_username',res.data.msg);
                    localStorage.setItem('is_Super',false);
                    that.$router.go(0)
                }
            })
        },
      handleCommand (command) {
        if (command === 'loginout') {
          localStorage.removeItem('ms_username');
          this.$ajax.get('/logout');
          this.$router.push('/')
        }
      },
      switchMedia (val) {
        this.media = val
        let that = this
        that.$ajax.get('/workbench/changeselecttype?type=' + val)
          .then(function(res) {
            that.closeAllTabFunc()
            that.freshTabFunc('Home')
          })
          .catch(function(err) {
              that.$message.error('')
          })
      },
      ...mapMutations('navTabs', [   // 
          'freshTabFunc'
      ]),
      ...mapMutations('navTabs', [   // 
          'closeAllTabFunc'
      ])
    }
  }
</script>



